@use "../common/variables.scss";

.slider {
  --cat-width: 140px;
  position: relative;
  display: grid;
  grid-template-columns: var(--cat-width) 1fr;

  @media (max-width: variables.$mobile) {
    width: 280px;
    margin: 0 auto;
  }

  @media (min-width: variables.$tablet) {
    --cat-width-tablet: 280px;
    grid-template-columns: var(--cat-width-tablet) 1fr;
  }
}

.slider__image-wrapper {
  display: flex;
  overflow: hidden;

  &--before {
    justify-content: flex-start;
  }

  &--after {
    justify-content: flex-end;
  }
}

.slider__image {
  width: 280px;
  height: 256px;

  @media (min-width: variables.$tablet) {
    width: 560px;
    height: 512px;
  }
}

.slider__change-image {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 100%;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  background-color: #666666;
  color: #666666;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: variables.$color-primary;
    color: variables.$color-primary;
    outline: none;
  }

  &:active {
    background-color: #5eaa2f;
    color: #5eaa2f;
    opacity: 0.6;
  }
}

.slider__toggle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
}
